<template>
  <div>
    <Table stripe border :columns="cols" :data="carriers">
      <template slot-scope="{ row }" slot="operator">
        <Button type="success" @click="showDetail(row.ID)">detail</Button>
      </template>
    </Table>
    <v-loading :show="loading"></v-loading>
  </div>
</template>

<script>
import VLoading from "@/components/public/v-loading";
export default {
  name: "carriersList",
  components: {VLoading},
  data : function (){
    return {
      loading : false,
      carriers : [],
      cols : [],
      currentPage : 0,
      pageSize : 0,
      total : 0
    }
  },
  beforeMount() {
    this.initPage();

  },
  methods : {
    showDetail : function (id){
      this.$router.push({name:"carrier", query:{id:id}});
    },
    initPage : function (){
      this.initCols();
      var postData = {
        currentPage : this.currentPage,
        pageSize : this.pageSize
      }
      var _this = this;
      this.loading = true;
      this.$axios.post("/home/initPage", postData).then(function (res){
        _this.loading = false;
        _this.carriers = res.data.retMap.carriers;
        _this.total = res.data.retMap.total;
      }).catch(function (res){
        _this.loading = false;
        console.log(res);
      })
    },
    initCols :function (){
      var cols = new Array();
      cols.push({
        title : this.$t("CAT"),
        key : "CAT"
      });
      cols.push({
        title : this.$t("NAME"),
        key : "NAME"
      })
      cols.push({
        title : this.$t("BACKBONE载体骨架"),
        key : "BACKBONE载体骨架"
      })
      cols.push({
        title : this.$t("PROMOTER"),
        key : "PROMOTER"
      })
      cols.push({
        title : this.$t("EXPRESSIONCASSETTE表达框"),
        key : "EXPRESSIONCASSETTE表达框"
      })
      cols.push({
        title : this.$t("operator"),
        slot : "operator"
      })
      this.cols = cols;
    }
  }
}
</script>

